<template>
    <div id="news">

        <v-btn color="blue darken-3" to="/news/add" class="ml-0" dark>Добавить</v-btn>

        <v-card class="mt-3">
            <v-list three-line v-for="(item, index) in news" :key="index">
                <template >
                    <v-list-tile>
                        <v-list-tile-avatar class="mt-0">
                            <v-avatar color="blue darken-4" size="25">
                                <span class="white--text subheader">{{item.year}} </span>
                            </v-avatar>
                        </v-list-tile-avatar>

                        <v-list-tile-content class="mt-0">
                            <v-list-tile-title v-html="item.title"></v-list-tile-title>
                            <v-list-tile-sub-title v-html="item.newsText"></v-list-tile-sub-title>
                            <v-list-tile-sub-title>Дедлайн: {{item.deadline}}</v-list-tile-sub-title>
                        </v-list-tile-content>

                        <v-list-tile-avatar class="mt-0 right">
                            <v-layout column>
                                <v-flex xs12 sm3>
                                    <v-btn @click="deleteNews(index)" flat icon color="grey darken-4">
                                        <v-icon>delete</v-icon>
                                    </v-btn>
                                </v-flex>
                                <v-flex xs12 sm3>
                                    <v-btn flat icon color="blue darken-2">
                                        <v-icon>edit</v-icon>
                                    </v-btn>
                                </v-flex>
                            </v-layout>
                        </v-list-tile-avatar>


                    </v-list-tile>
                    <v-divider class="mt-0"/>
                </template>
            </v-list>
        </v-card>
    </div>
</template>

<script>
    import Icon from "vue-awesome/components/Icon";

    export default {
        components: {Icon},
        data() {
            return {
                news: [
                    {
                        id:1,
                        newsText: 'tgest',
                        title: "titgggggggggffffftitgggggggggffffftitgggggggggffff" +
                        "ftitgggggggggffffftitgggggggggffffftitgggggggggffffftitggg" +
                        "ggggggffffftitgggggggggffffftitgggggggggffffftitgggggggggfffffti" +
                        "tgggggggggffffftitgggggggggffffftitgggggggggffffftitgggggggggfffff" +
                        "titgggggggggfffff",
                        course: {},
                        year: 3,
                        deadline: 454685412
                    },
                    {
                        id:2,
                        newsText: 'testtesttesttesttesttesttest' +
                        'testtesttesttesttesttesttesttesttesttesttesttest' +
                        'testtesttesttesttesttesttesttest' +
                        'testtesttesttesttesttesttesttesttesttesttesttesttest' +
                        'testtest',
                        title: "title",
                        course: {},
                        year: 2,
                        deadline: 454685412
                    },
                    {
                        id:3,
                        newsText: 'test',
                        title: "title",
                        course: {},
                        year: 1,
                        deadline: 454685412
                    }
                ]
            }
        }, methods: {
            deleteNews(index){
                this.news.splice(index,1);
            }
        }
        // // Fetches posts when the component is created.
        // created() {
        //     axios.get(`http://jsonplaceholder.typicode.com/posts`)
        //         .then(response => {
        //             this.posts = response.data
        //         })
        //         .catch(e => {
        //             this.errors.push(e)
        //         })
        // }
    }

</script>
