<template>
    <div><p class="headline primary--text">Добавить новость</p>

        <form class="mt-3">
            <v-text-field
                    v-model="title"
                    label="Заголовок"
                    :error-messages="errors.collect('title')"
                    v-validate="'required'"
                    data-vv-name="title"
                    required>

            </v-text-field>

            <v-text-field
                    v-model="newsText"
                    label="Текст"
                    v-validate="'required'"
                    multi-line
                    rows="2"
                    :error-messages="errors.collect('newsText')"
                    auto-grow
                    data-vv-name="newsText"
                    required>
            </v-text-field>

            <v-flex xs12 sm6>
                <v-select
                        :items="years"
                        v-model="year"
                        label="Год обучения"
                        single-line
                        prepend-icon="grade"
                ></v-select>
            </v-flex>
            <v-menu
                    ref="deadline"
                    lazy
                    :close-on-content-click="false"
                    v-model="deadline"
                    transition="scale-transition"
                    offset-y
                    full-width
                    :nudge-right="40"
                    min-width="290px"
                    :return-value.sync="date">
                <v-text-field
                        v-model="date"
                        slot="activator"
                        label="Дедлайн"
                        required
                        prepend-icon="event"
                        readonly>
                </v-text-field>

                <v-date-picker v-model="date" no-title scrollable required>
                    <v-spacer></v-spacer>
                    <v-btn flat color="primary" @click="deadline = false">Cancel</v-btn>
                    <v-btn flat color="primary" @click="$refs.deadline.save(date)">OK</v-btn>
                </v-date-picker>
            </v-menu>


            <v-layout row class="mt-5 right">
                <v-btn @click="submit">submit</v-btn>
                <v-btn @click="clear">clear</v-btn>
            </v-layout>
        </form>
    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        data: () => ({
            years: [1, 2, 3, 4],
            year: '0',

            date: null,
            deadline: false,

            newsText: '',
            title: '',

            valid: true,
            requiredRules: [
                v => !!v || 'Это поле обязательно для заполнения'
            ],
            dictionary: {
                custom: {
                    title: {
                        required: () => 'Введите заголовок',
                    },
                    newsText: {
                        required: () => 'Введите текст',
                    },
                    date: {
                        required: () => 'Выберите дату дедлайна',
                    }, deadline: {
                        required: () => 'Выберите дату дедлайна',
                    }
                }
            }
        }),
        mounted() {
            this.$validator.localize('ru', this.dictionary)
        },
        methods: {
            submit() {
                if (this.$validator.validateAll()) {
                    // Native form submission is not yet supported
                    axios.post('/api/submit', {
                        name: this.name,
                        email: this.email,
                        select: this.select,
                        checkbox: this.checkbox
                    })
                }
            },
            clear() {
                this.$refs.form.reset()
            }

        }
    }
</script>

<codepen-resources lang="json">
    {
    "js": ["https://unpkg.com/vee-validate@latest/dist/vee-validate.js"]
    }
</codepen-resources>



